import './ApartmentInfo.css';
const ApartmentInfo = ({ apartment }) => {
    return (
      <div className='apartment-info-container'>
        <div className="apartment-info">
        <div className="apartment-header">
          <h2>Villa</h2>
          <span className="apartment-size">{apartment.size}m²</span>
        </div>
        <table className="apartment-table">
          <tbody>
            <tr>
              <td>Reference No.</td>
              <td>{apartment.referenceNumber}</td>
            </tr>
            <tr>
              <td>Bedrooms</td>
              <td>{apartment.bedrooms}</td>
            </tr>
            <tr>
              <td>Bathrooms</td>
              <td>{apartment.bathrooms}</td>
            </tr>
            <tr>
              <td>Delivery In</td>
              <td>{apartment.deliveryIn}</td>
            </tr>
            <tr>
              <td>Compound</td>
              <td><a href={apartment.compoundLink}>{apartment.compoundName}</a></td>
            </tr>
            <tr>
              <td>Sale Type</td>
              <td>{apartment.saleType}</td>
            </tr>
            <tr>
              <td>Finishing</td>
              <td>{apartment.finishing}</td>
            </tr>
          </tbody>
        </table>
      </div>
      </div>
    );
  };
  
  export default ApartmentInfo;