
import{ useRef} from 'react'
import { useGLTF } from '@react-three/drei'
import {useFrame} from '@react-three/fiber'

export default function GroundFloor(props) {
  const { nodes, materials } = useGLTF(`${process.env.PUBLIC_URL}/models/groundFloor.glb`)
 // const [active, setActive] = useState(false)
    const groupRef = useRef()
    var DisplayText = '300 SM with private pool and back yard'
    useFrame((state, delta) => (groupRef.current.rotation.y += delta*0.125))
  return (
    
    <group {...props} 
    onPointerOver={(event) => props.onPointerOver(event, DisplayText)}
      onPointerOut={props.onPointerOut}
    ref={groupRef} dispose={null}>
      <group>
        <mesh geometry={nodes['Line002_16_-_Matte_Plastic_0'].geometry} material={materials['01_-_Default']} />
        <mesh geometry={nodes['Line002_16_-_Matte_Plastic_0_1'].geometry} material={materials.Ceramic} />
        <mesh geometry={nodes['Line002_16_-_Matte_Plastic_0_2'].geometry} material={materials.Solid_Glass} />
        <mesh geometry={nodes['Line002_16_-_Matte_Plastic_0_3'].geometry} material={materials.Water} />
        <mesh geometry={nodes['Line002_16_-_Matte_Plastic_0_4'].geometry} material={materials['13_-_Brushed_Metal_2']} />
        <mesh geometry={nodes['Line002_16_-_Matte_Plastic_0_5'].geometry} material={materials.Grass} />
      </group>
    </group>
  )
}
/*
      
scale={active ? 1.5  : 1}
onClick={(event) => setActive(!active)}
*/
useGLTF.preload('/models/groundFloor.glb')
