import React, { Suspense } from 'react'
import {Canvas} from '@react-three/fiber'
import{ useRef, useState } from 'react'
import GroundFloor from './GroundFloor'
import FirstFloor from './FirstFloor'
import { OrbitControls } from '@react-three/drei'
import ApartmentInfo from './ApartmentInfo'
import SecondFloor from './SecondFloor'
import './Showbox.css';

export default function Showbox() {
  const apartmentData = {
    size: 300,
    referenceNumber: 202005,
    bedrooms: 3,
    bathrooms: 3,
    deliveryIn: 'Delivered',
    compoundLink: 'https://example.com/downtown-new-alamein',
    compoundName: 'Downtown New city',
    saleType: 'Developer Sale',
    finishing: 'Finished',
  };
  const ref = useRef()
  const [hoverText, setHoverText] = useState({ 
    visible: false,
    x: 0,
    y: 0,
    text: 'information' 
  });
  const handlePointerOver = (e,newText) => {
    setHoverText({ 
      visible: true, 
      x: e.clientX + 10, 
      y: e.clientY + 10, 
      text: newText || 'information'
    }); // Offset the text by 10px to avoid overlap
  };
  
  const handlePointerOut = () => {
    setHoverText({ ...hoverText, visible: false });
  };
  
const [selectedFloor, setSelectedFloor] = useState(null);

const handleClick = (floorName) => {
  setSelectedFloor(floorName);
  // Later: trigger camera move too
};

const closeModal = () => {
  setSelectedFloor(null);
};

  return (
    <div className='property-card'>
      <div className='property-name'>
        <h2>Villa for sale in Downtown New city with 3 bedrooms in Al city by City Civil Developments</h2>
        <h2>15,607,000 EGP</h2>
        <h2> 300 m²</h2>
      </div>
      <div className='showBox' >
      {hoverText.visible && (
        <div 
          style={{
            left: `${hoverText.x}px`,
            top: `${hoverText.y}px`,
          }}
          className='infoDiv' 
        >
          {hoverText.text}
        </div>
      )}
    <div className={`canvas-container ${selectedFloor ? '' : 'full-width'}`}>
    <Canvas  camera={{ position: [0, 5, 10] }} >
      <ambientLight intensity={Math.PI / 2} />
      <spotLight position={[10, 10, 10]} angle={0.45  } penumbra={1} decay={0} intensity={Math.PI} />
      <pointLight position={[-10, -10, -10]} decay={0} intensity={Math.PI} />
      <Suspense>
        
      <GroundFloor scale ={0.5} onPointerOver={handlePointerOver} onPointerOut={handlePointerOut} onClick={() => handleClick('ground Floor')}/>
      <FirstFloor scale ={0.5} onPointerOver={handlePointerOver} onPointerOut={handlePointerOut}  onClick={() => handleClick('first Floor')}/>
      <SecondFloor scale ={0.5} onPointerOver={handlePointerOver} onPointerOut={handlePointerOut} onClick={() => handleClick('second Floor')}/>
      </Suspense>
      <OrbitControls ref={ref} enabled={!selectedFloor} />

    </Canvas>
    </div>
    {selectedFloor && (
    <div className="info-panel">
      <button className="close-btn" onClick={closeModal}>×</button>
      <h2>{selectedFloor}</h2>
      <p>This is a detailed view of the {selectedFloor.toLowerCase()}.</p>
      <img 
        src={`/images/floorplans/${selectedFloor.replace(/\s/g, '').toLowerCase()}.png`} 
        alt={`${selectedFloor} floor plan`} 
        className='floorplan-img'
      />
    </div>
  )}
    </div>
      
      <ApartmentInfo apartment={apartmentData} />
    </div>
  )
}

