import{ useRef } from 'react'
import { useGLTF } from '@react-three/drei'
import {useFrame} from '@react-three/fiber'

export default function FirstFloor(props) {
  const { nodes, materials } =useGLTF(`${process.env.PUBLIC_URL}/models/firstFloor.glb`)
  //const [active, setActive] = useState(false)
    const groupRef = useRef()
    var DisplayText = 'Ground Floor: Entrance, Reception, Guest Bathroom, kitchen and back yard porch'
    useFrame((state, delta) => (groupRef.current.rotation.y += delta*0.125))
  return (
    
    <group {...props} 
    onPointerOver={(event) => props.onPointerOver(event, DisplayText)}
      onPointerOut={props.onPointerOut}
    ref={groupRef} dispose={null}>
      <mesh geometry={nodes['Line002_16_-_Matte_Plastic_0003'].geometry} material={materials['03_-_Default']} />
      <mesh geometry={nodes['Line002_16_-_Matte_Plastic_0003_1'].geometry} material={materials['04_-_Default']} />
      <mesh geometry={nodes['Line002_16_-_Matte_Plastic_0003_2'].geometry} material={materials['13_-_Brushed_Metal_2']} />
      <mesh geometry={nodes['Line002_16_-_Matte_Plastic_0003_3'].geometry} material={materials.Metal} />
      <mesh geometry={nodes['Line002_16_-_Matte_Plastic_0003_4'].geometry} material={materials['15_-_Glossy_Plastic']} />
      <mesh geometry={nodes['Line002_16_-_Matte_Plastic_0003_5'].geometry} material={materials['19_-_Glass_Thin_wall']} />
      <mesh geometry={nodes['Line002_16_-_Matte_Plastic_0003_6'].geometry} material={materials['17_-_Old_Copper_1']} />
      <mesh geometry={nodes['Line002_16_-_Matte_Plastic_0003_7'].geometry} material={materials['18_-_Rubber']} />
      <mesh geometry={nodes['Line002_16_-_Matte_Plastic_0003_8'].geometry} material={materials.Glass} />
    </group>
  )
}

useGLTF.preload('models/firstFloor.glb')
