
import{ useRef } from 'react'
import { useGLTF } from '@react-three/drei'
import {useFrame} from '@react-three/fiber'


export default function SecondFloor(props) {
  const { nodes, materials } = useGLTF(`${process.env.PUBLIC_URL}/models/secondFloor.glb`)
 // const [active, setActive] = useState(false)
    const groupRef = useRef()
    var DisplayText = 'First Floor: 3 rooms front and back patio 2 bathrooms'
    useFrame((state, delta) => (groupRef.current.rotation.y += delta*0.125))
  return (
    
    <group {...props} 
    onPointerOver={(event) => props.onPointerOver(event, DisplayText)}
      onPointerOut={props.onPointerOut}
    ref={groupRef} dispose={null}>
      
      <mesh geometry={nodes['Line002_16_-_Matte_Plastic_0002'].geometry} material={materials['03_-_Default']} />
      <mesh geometry={nodes['Line002_16_-_Matte_Plastic_0002_1'].geometry} material={materials['01_-_Default']} />
      <mesh geometry={nodes['Line002_16_-_Matte_Plastic_0002_2'].geometry} material={materials.Wall_Paint} />
      <mesh geometry={nodes['Line002_16_-_Matte_Plastic_0002_3'].geometry} material={materials['18_-_Rubber']} />
      <mesh geometry={nodes['Line002_16_-_Matte_Plastic_0002_4'].geometry} material={materials.Glass} />
      <mesh geometry={nodes['Line002_16_-_Matte_Plastic_0002_5'].geometry} material={materials['21_-_Default']} />
    </group>
  )
}

useGLTF.preload('/models/secondFloor.glb')
